footer {
	background-image: url('~/src/utils/assets/images/header-bg.jpg');
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	box-shadow: 0 0 10px var(--tertiary__color);
	z-index: 99;
	padding: 20px 20px 0 20px;
	text-align: center;
	font-size: 14px;
	font-weight: 300;
	line-height: 1.5;
	bottom: 0;
}

footer .footer__container {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	height: 225px;
	position: relative;
	height: 100%;
	width: 100%;
	margin-bottom: 2rem;
}

footer .footer__container .footer__content {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: space-around;
	height: 100%;
	width: 100%;
}

footer .footer__container .footer__content .footer__content__left {
	display: flex;
	flex-direction: column;
}
footer .footer__container .footer__content .footer__content__left .footer__content__left__logo {
	display: flex;
	align-items: center;
	max-width: 200px;
}
footer .footer__container .footer__content .footer__content__left .footer__content__left__text p {
	color: var(--secondary__color);
}
footer .footer__container .footer__content .footer__content__left .footer__content__left__logo a img {
	width: 100%;
}

footer .footer__container .footer__content .footer__content__right {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	line-height: normal;
	margin: 0;
}
footer .footer__container .footer__content .footer__content__right h2 {
	color: var(--secondary__color);
}

footer .footer__container .footer__content .footer__content__right .direct__contact {
	display: flex;
	flex-direction: row;
	gap: 3rem;
}
footer .footer__container .footer__content .footer__content__right .direct__contact h3 {
	color: var(--active__link);
}
footer .footer__container .footer__content .footer__content__right .direct__contact h3 ~ a,
footer .footer__container .footer__content .footer__content__right .direct__contact h3 ~ p {
	color: var(--secondary__color);
	text-decoration: none;
}
footer .footer__container .footer__content .footer__content__right .direct__contact .svg-inline--fa {
	margin-right: 1rem;
	color: var(--tertiary__color);
}

footer .footer__bottom {
	height: 40px;
	margin: 0 -20px;
	padding: 0 20px;
	width: 100%;
	background-color: transparent;
	color: var(--secondary__color);
	bottom: -20px;
}

footer .footer__bottom p {
	margin: 0;
	padding: 10px 0;
	text-align: center;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
	footer .footer__container {
		flex-direction: column;
		height: fit-content;
	}

	footer .footer__container .footer__content {
		flex-direction: column;
		align-items: center;
		gap: 1rem;
	}

	footer .footer__container .footer__content .footer__content__right .footer__content__right__contact__text {
		display: flex;
		flex-direction: column;
		align-items: center;
		/* gap: 1rem; */
	}

	footer
		.footer__container
		.footer__content
		.footer__content__right
		.footer__content__right__contact__text
		.direct__contact {
		display: flex;
		flex-direction: column;
		gap: 1rem;
	}

	footer .footer__bottom {
		margin-top: 2rem;
	}
}

